// ** Table Header
const CustomHeader = ({
  handlePerPage,
  rowsPerPage,
  handleFilter,
  searchTerm
}) => {
  const dispatch = useDispatch()
  const store = useSelector((state) => state.rep)
  console.log(store.seriesid)
  const download = () => {
    dispatch(getcricketplayerdownload(store.seriesid, store.fixtureid)) //filterid[0]?.id, filterid2[0]?.id
    console.log(store.seriesid)
  }

  return (
    <div className="invoice-list-table-header w-100 mr-1 ml-50 mt-2 mb-75">
      <Row>
        <Col xl="6" className="d-flex align-items-center p-0">
          <div className="d-flex align-items-center w-100">
            <Label for="rows-per-page">Show</Label>
            <CustomInput
              className="form-control mx-50"
              type="select"
              id="rows-per-page"
              value={rowsPerPage}
              onChange={handlePerPage}
              style={{
                width: '5rem',
                padding: '0 0.8rem',
                backgroundPosition:
                  'calc(100% - 3px) 11px, calc(100% - 20px) 13px, 100% 0'
              }}
            >
              <option value="10">10</option>
              <option value="25">25</option>
              <option value="50">50</option>
            </CustomInput>
            <Label for="rows-per-page">Entries</Label>
          </div>
        </Col>
        <Col
          xl="6"
          className="d-flex align-items-sm-center justify-content-lg-end justify-content-start flex-lg-nowrap flex-wrap flex-sm-row flex-column pr-lg-1 p-0 mt-lg-0 mt-1"
        >
          <div className="d-flex align-items-center mb-sm-0 mb-1 mr-1">
            <Label className="mb-0" for="search-invoice">
              Search:
            </Label>
            <Input
              id="search-invoice"
              className="ml-50 w-100"
              type="text"
              value={searchTerm}
              onChange={(e) => handleFilter(e.target.value)}
            />
          </div>
          <Button
                color ='primary'
                onClick ={() => download()}
                // onClick = {getfootballcontestdownload()}
                >
                  Download CSV
                </Button>
        </Col>
      </Row>
    </div>
  )
}

const UsersList = () => {
  // ** Store Vars
  const store = useSelector((state) => state.rep)
  // ** States
  const [searchTerm, setSearchTerm] = useState('')
  const [listData, setListData] = useState([])
  const [currentPage, setCurrentPage] = useState(1)
  const [rowsPerPage, setRowsPerPage] = useState(10)
  // const [sidebarOpen, setSidebarOpen] = useState(false)
  // const history = useHistory()

  useEffect(() => {
    if (store.player_reports) {
      setListData(store.player_reports)
      console.log(store.player_reports.length)
    }
  }, [store.player_reports])
  console.log(store)
  // ** User filter options

  // ** Function in get data on page change
  const handlePagination = (page) => {
    setCurrentPage(page.selected + 1)
  }

  // ** Function in get data on rows per page
  const handlePerPage = (e) => {
    const value = parseInt(e.currentTarget.value)
    setRowsPerPage(value)
    setCurrentPage(1)
  }

  // ** Function in get data on search query change
  const handleFilter = (val) => {
    setSearchTerm(val)
  }

  useEffect(() => {
    if (searchTerm) {

      const filterList = store.player_reports.records.filter((userItem) => {
        const newval =
                userItem.full_name.toLowerCase().startsWith(searchTerm.toLowerCase()) ||
                // (userItem && userItem.mobile.toString().toLowerCase().startwith(searchTerm.toString().toLowerCase())) ||
                userItem.email.toLowerCase().startsWith(searchTerm.toLowerCase()) 

        const includes =
        userItem.full_name.toLowerCase().includes(searchTerm.toLowerCase()) ||
        userItem.email.toLowerCase().includes(searchTerm.toLowerCase()) 
        // userItem.mobile.toString().toLowerCase().includes(searchTerm.toLowerCase()) 
        
        if (newval) {
          return newval
          // return userItem.full_name.toLowerCase().includes(searchTerm.toLowerCase())
        } else if (!newval && includes) {
          return includes
        } else return null
      })

      setListData(filterList)
      setCurrentPage(1)
    } 
  }, [searchTerm])

  
//   if (store.loadAllUsers) {
//     return (
//       <div className="position-absolute d-flex justify-content-center align-items-center w-100 position-top-0 position-bottom-0 position-left-0 position-right-0 h-100">
//         <UiLoader
//           blocking
//         />
//       </div>
//     )
//   }


  // ** Custom Pagination
  const CustomPagination = () => {
    const count = Number(Math.ceil(store.player_reports.record_count / rowsPerPage))

    return (
      <ReactPaginate
        previousLabel={''}
        nextLabel={''}
        pageCount={count || 1}
        activeClassName="active"
        forcePage={currentPage !== 0 ? currentPage - 1 : 0}
        onPageChange={(page) => handlePagination(page)}
        pageClassName={'page-item'}
        nextLinkClassName={'page-link'}
        nextClassName={'page-item next'}
        previousClassName={'page-item prev'}
        previousLinkClassName={'page-link'}
        pageLinkClassName={'page-link'}
        containerClassName={
          'pagination react-paginate justify-content-end my-2 pr-1'
        }
      />
    )
  }

  // ** Table data to render
  const dataToRender = () => {
    const filters = {
      q: searchTerm
    }

    const isFiltered = Object.keys(filters).some(function (k) {
      return filters[k].length > 0
    })

    // if (listData.length > 0) {
    //   return listData
    // } else
    if (listData.length === 0 && isFiltered) {
      return []
    } else if (listData.length) {
      return listData.slice((currentPage - 1) * rowsPerPage, currentPage * rowsPerPage)
    }
  }

  return (
    <Fragment>
      <Card>
        <DataTable
          noHeader
          pagination
          subHeader
          responsive
          highlightOnHover
          pointerOnHover
          paginationServer
          columns={columns}
          paginationPerPage={rowsPerPage}
          // onRowClicked={(data) => {
          //   history.push(`users/${data.s_no}`)
          // }}
          sortIcon={<ChevronDown />}
          paginationDefaultPage={currentPage}
          className="react-dataTable"
          paginationComponent={CustomPagination}          
          data={dataToRender()}
          subHeaderComponent={
            <CustomHeader
              handlePerPage={handlePerPage}
              rowsPerPage={rowsPerPage}
              searchTerm={searchTerm}
              handleFilter={handleFilter}
            />
          }
        />
      </Card>
    </Fragment>
  )
}

export default UsersList
